  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{
  width: 100%;
  margin: 0;
  padding:0;
  overflow-x: hidden; 
  
}
.App {
  text-align: center;
}
p {
  text-align: left;
}
h3 {
  margin-bottom: 5px;
  color: #ebebeb;
}
