.contact_form_container {
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 1000px;
  min-height: 100vh;
}
.contact_form_container p, 
.contact_form_container ul, 
.contact_form_container li, 
.contact_form_container a, 
.contact_form_container a:visited, 
.contact_form_container a:link{
  color: #ffffff;
}
.form_submit_button:hover{
  background: #ffffff42;
}
.contact_background{
    background-image:linear-gradient(to bottom, #0000995d 0%, #33ccff65 100%),url(/static/media/contact-background.9fe6f6c0.jpg);
    background-size: cover;
    background-position: center;
}
.contact_text {
  font-size: 20px;
  max-width: 350px;
  text-align: justify;
  margin: 20px;
}
.contact_text p{
  text-align: justify;
}
.contact_text ul {
  padding: 0;
  margin: 0;
  font-size: 23px;
}
.contact_form {
  width: 300px;
  display: flex;
  flex-direction: column;
}

.name_label,
.email_label,
.message_label {
  text-align: justify;
  padding: 0.5em;
  font-size: 1em;
}

.name_input,
.email_input,
.message_input {
  padding: 10px;
  height: 20px;
  font-size: 18px;
  border: 0px solid #d3d3d3;
  background: #f5f5f5;
  border-radius: 5px;
}
.name_input,
.email_input{
   margin-bottom: 30px;
}
.message_input{
  margin-bottom: 10px;
}

.message_input {
  min-height: 120px;
}

.form_submit_button {
  display: block;
  color: #ffffff;
  margin-left: 200px;
  padding: 10px 25px;
  width: 100px;
  display: inline-block;
  border: 1px solid #acacac;
  background: transparent;
  font-size: 1em;
  border-radius: 5px;
}

a {
  text-decoration: none;
  color: #000000;
}
.contact_text li{
  list-style: none;
  padding: 5px;
  font-size: 20px;

}

.contact_logo{
  height: 20px;
  width: 20px;
  padding-right: 5px;
  vertical-align: middle;
}

@media all and (min-width: 600px) {
  .contact_form_container {
    flex-direction: row;
    justify-content: space-around;
  }
  .contact_text h2 {
    margin-top: 0;
  }
  .contact_text {
    flex: 1 1;
    max-width: 350px;
  }
  .contact_form {
    flex: 1 1;
    max-width: 350px;
  }

  .contact_text ul {
    padding: 20px;
  }
}

.project_main_container {
  display: flex;
  justify-content: center;
  margin: auto;
  min-height: 100vh;
}
.center {
  text-align: center;
  color: #5e5e5e;
}
.project_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 300px;
  background-image: linear-gradient(to bottom, #f1f1f1 0%, #f8f8f8 100%);
  color: #000000;
  box-shadow: 1px 1px 4px #bbbbbb;
  border-radius: 15px;
  margin: 20px 15px 10px 15px;
  padding: 10px 20px 10px 20px;
  height: 600px;
}
.project_container p {
  text-align: justify;
}
.project_container h2, p, h3, ul{
  margin: 4px 0 4px 0;
}
.project_image {
  flex: 1 1;
  width: 220px;
  height: 220px;
  margin: auto;
  /* border:1px solid #ffffff; */
  border-radius: 50%;
}
.blackjack:hover {
  box-shadow: 1px 1px 2px rgb(175, 175, 175), 0 0 10px #f7be0241, 0 0 5px #d1700241;
}
.repetition:hover {
  box-shadow: 1px 1px 2px rgb(102, 102, 102), 0 0 10px #2cdde636,
    0 0 5px #2ccaff38;
}
.battleship:hover {
  box-shadow: 1px 1px 2px rgb(0, 0, 0), 0 0 10px #80138c8c, 0 0 5px #5409917c;
}
.buttons {
  display: inline-flex;
  justify-content: center;
}
.links {
  border: 1px solid #c2c2c2;
  width: 100px;
  height: 25px;
  margin: 0 10px 0 10px;
  padding: 5px;
  border-radius: 4px;
  box-shadow: 1px 1px 2px #dbdbdb;
}
.links:hover {
  border: 1px solid #3f3f3f;
}
.links:active {
  background: #ffffff;
}
.project_main_container ul {
  text-align: left;
  list-style-type: none;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
}
@media screen and (max-width: 550px) {
  .project_main_container {
    flex-direction: column;
  }
  .project_container {
    margin: 10px 20px 10px 20px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    align-items: center;
    max-width: none;
  }
}

.aboutme_section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}
.aboutme_section h2 {
  text-align: center;
}
.aboutme_section p {
  padding: 0 20px 0 20px;
}
.aboutme_content_container {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  margin: auto;
  max-width: 600px;
}
.aboutme_section fieldset {
    padding: 10px 20px 10px 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 20px auto;
  border: 1px solid #c4c4c4;
}
.aboutme_image {
  width: 200px;
  height: 200px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  margin: 0 20px 0 20px;
}

.aboutme_text {
  width: 300px;
  margin: auto;
}
.aboutme_text p {
  text-align: justify;
}

@media all and (max-width: 550px) {
  .aboutme_section {
    display: flex;
    justify-content: space-evenly;
  }
  .aboutme_image{
    margin: 20px 20px 10px 20px;
  }
  .aboutme_text {
  }
  .aboutme_content_container {
    flex-direction: column;
  }
}

.homepage_container {
  color: #ffffff;
  text-shadow: 1px 1px 2px #555555;
  display: flex;
  width: inherit;
  align-content: center;
  align-items: center;
  justify-content: center;
  margin: auto;
  flex-direction: column;
  height: 100vh;
}

.homepage_container {
  background-image: linear-gradient(to bottom, #000099c0 0%, #33ccffc0 100%),
    url(/static/media/technology.3312b9a3.jpg);
  background-size: cover;
  background-position: center;
}

h1 {
  font-size: 45px;
  margin: 2px;
}
.homepage_container h2 {
  font-size: 22px;
}

.homepage_container span {
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

@media all and (min-width: 550px) {
  h1 {
    font-size: 60px;
  }
}

.skills_container {
    display: flex;
    flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
    margin: auto;
    min-height: 100vh;
}
.skills_container legend{
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 3px;
  padding: 0 10px 0 px;

}
.skills_container fieldset{
  padding: 10px 20px 10px 20px;
  border: 1px solid #c4c4c4;
}
.skills_image{
  height: 75px;
  width: 75px;
}
.learned_skills{
  margin: 0;
  padding: 0;
  justify-content: center;
  list-style-type: none;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
}
.skills_container{
  align-items: center;
}
.learned_skills li{
  display: flex;
  width: 250px;
  padding: 10px 20px;
  margin: 0 20px;
}
.learned_skills li div{
  margin: auto;
  font-size: 25px;
  font-weight: 800;
}
.loading {
    margin: 0px;
}
.animation_text{
  font-size: 16px;
}
.animation_text_p{
  font-size: 16px;
  font-weight: 500;
}
  .loading span {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 10px;
    background: #111DA7;
    border-radius: 50%;
    -webkit-animation: loading 1s infinite alternate;
            animation: loading 1s infinite alternate;
  }

  .loading span:nth-of-type(2) {
    background: #1A37B3;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
  }
  .loading span:nth-of-type(3) {
    background: #2252BF;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }
  .loading span:nth-of-type(4) {
    background: #2B6CCC;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
  }
  .loading span:nth-of-type(5) {
    background: #3386D8;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
  }
  .loading span:nth-of-type(6) {
    background: #3BA1E4;
    -webkit-animation-delay: 1.0s;
            animation-delay: 1.0s;
  }
  .loading span:nth-of-type(7) {
    background: #44BBF0;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
  }
  @-webkit-keyframes loading {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes loading {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @media all and (max-width:500px) {
    .learned_skills{
      display: flex;
      flex-direction: column;
      margin: auto;
      text-align: center;
      
    }
    .skills_container fieldset{
      margin: 50px 20px 20px 20px;
    }
    .learned_skills li{
      margin: auto;
    }
  }
